import { Button, Grid } from "@material-ui/core"
import axios from "axios";
import { useEffect, useState } from "react";
import { useHistory } from "react-router";
import config from "../../../Configs/Configs";
import AutoComplete from "../AutoComplete/AutoComplete"
import "./NavBar.css"
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import { NavLink } from "react-router-dom";
import LogoImg from "../../../Assets/logo.jpg";
import SearchIcon from '@mui/icons-material/Search';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import FacebookIcon from '@mui/icons-material/Facebook';
function NavBar(): JSX.Element {
    const [open ,setOpen]=useState<boolean>(false);
    const [openSearch ,setOpenSearch] =useState<boolean>(false);
    const history =useHistory();
    const loginHandler = () => {
        history.push("/login")
    }
    const logoutHandler = () => {
        axios.post(config.urls.login + "logout");
        sessionStorage.clear();
        history.push("/home");
    }
   const registerHandler =()=>{
       history.push("/register")
   }
  const MobileMenuHandler =()=>{
      setOpen(!open)
  }
    return (
        <div className="NavBar">
            <div className="LiftSide">
                <div className="Links" id={open ? "hidden" : ""}>
                    <ul className ="ListLinks">
                        <li><a href="/home">דף הבית</a> </li>
                        <li className ="DropMenu"><a href="/products">מוצרים</a>
                        <ul className ="SubMenu">
                            <li> <a href ="/clocks" >שעונים</a></li>
                            <li> <a href ="/rings" >טבעות</a></li>
                            <li> <a href ="/earings" >עגילים</a></li>
                            <li> <a href ="/chains" >שרשרת</a></li>
                            <li> <a href ="/bracelets" >צמיד</a></li>
                        </ul>
                        </li>
                        <li><a href="/home">חדשים</a></li>
                        <li> <a href="/about-us">אודות</a></li>
                        <li> <a href="/contact-us">צור קשר</a></li>
                        <li> <a href="/forum">פורום</a></li>
                        <li> <a href="/admin-page">כניסה מערכת</a></li>
                    </ul>
                </div>
                <div className="Menu">
                   <IconButton onClick={MobileMenuHandler}>
                       <MenuIcon  />
                   </IconButton>
                </div>
            </div>
            <div className="RightSide">
               <div className="Title">
                    <NavLink to="/home">YAM<img src={LogoImg} />ROT</NavLink>
                </div>
                <div className="SearchIconField">
                    <button onClick ={()=>{setOpenSearch(!openSearch)}}><SearchIcon /></button>
                    <button><WhatsAppIcon /></button>
                    <button><FacebookIcon /></button>
                </div>
               
                <div className="Search" id ={openSearch ? "hidden" : ""}>
                    <AutoComplete />
                </div>
            </div>

        </div>
    )
}



export default NavBar